import { useState } from 'react';
import IntlTelInput from 'react-intl-tel-input';
import 'react-intl-tel-input/dist/main.css';
import styles from './form.module.css';

function App() {
  const [loading, setLoading] = useState(false);
  const [dialCode, setDialCode] = useState('+52');
  const [data, setData] = useState({
    name: '',
    email: '',
    phone: ''
  });

  const handleChange = e => setData({ ...data, [e.target.name]: e.target.value });
  const handlePhoneChange = (_, num) => num.match(/^(\d+|)$/) ? setData({ ...data, phone: num }) : null;
  const handleDialCodeChange = (_, codeData) => setDialCode(`+${codeData.dialCode}`);

  const handleSubmit = e => {
    e.preventDefault();
    setLoading(true);

    fetch('https://leads-sheet-three.vercel.app/api/lead', {
      method: 'POST',
      body: JSON.stringify({ data: { ...data, phone: dialCode + data.phone, id: window.SHEET_ID } }),
      headers: {
        'content-type': 'application/json'
      }
    })
    .then(response => response.status === 201 ? true : Promise.reject(false))
    .then(() => window.SUBMIT_MESSAGE ? alert(window.SUBMIT_MESSAGE) : null)
    .then(() => window.REDIRECT_URL ? window.top.location.href = window.REDIRECT_URL : null)
    .catch(() => alert('Ha ocurrido un error al enviar los datos!'))
    .finally(() => setLoading(false));
  }

  return (
    <div className="leads-plugin">
      <div className={styles['new-lead-card']}>
        <form className={styles['new-lead-form']} onSubmit={handleSubmit}>
          <div className={styles['new-lead-form-field']}>
            <label htmlFor="lead-name">What's your name?</label>
            <input id="lead-name" name="name" placeholder="John Doe" value={data.name} onChange={handleChange} />
          </div>
          {window.SEND_EMAIL ? <div className={styles['new-lead-form-field']}>
            <label htmlFor="lead-email">Your best email?</label>
            <input id="lead-email" type="email" name="email" placeholder="example@email.com" value={data.email} onChange={handleChange} />
          </div> : null}
          {window.SEND_PHONE ? <div className={styles['new-lead-form-field']}>
            <label htmlFor="lead-phone">And your Cell Phone?</label>
            <IntlTelInput
              preferredCountries={['us', 'mx']}
              inputClassName={styles['lead-phone']}
              value={data.phone}
              onPhoneNumberChange={handlePhoneChange}
              onSelectFlag={handleDialCodeChange}
            />
          </div> : null}
          <button type="submit" className={styles['new-lead-submit']} disabled={loading}>{loading ? 'Sending' : 'Send'}</button>
        </form>
      </div>
    </div>
  );
}

export default App;
